<template>
  <div class="temp4">
    <div class="content" style="line-height: 44px;">
      <div class="form-group flex-row">
        <label style="width: 200px;">血生化化验检查日期：</label>
        <input type="date" v-model="date" />
      </div>
      <div class="form-group flex-row">
        <label style="width: 200px;">肌酐(Cr)</label>
        <input type="number" v-model="Cr" /><span class="unit">umol/L</span>
      </div>
      <div class="form-group flex-row">
        <label style="width: 200px;">尿素氮(BUN)</label>
        <input type="number" v-model="BUN" /><span class="unit">umol/L</span>
      </div>
      <div class="form-group flex-row">
        <label style="width: 200px;">尿酸(UA)</label>
        <input type="number" v-model="UA" /><span class="unit">umol/L</span>
      </div>
      <div class="form-group flex-row">
        <label style="width: 200px;">血糖(Glu)</label>
        <input type="number" v-model="Glu" /><span class="unit">mmol/L</span>
      </div>
      <div class="form-group flex-row">
        <label style="width: 200px;">总胆固醇(TCHO)</label>
        <input type="number" v-model="Tcho" /><span class="unit">mmol/L</span>
      </div>
      <div class="form-group flex-row">
        <label style="width: 200px;">甘油三酯(TG)</label>
        <input type="number" v-model="Tg" /><span class="unit">mmol/L</span>
      </div> 
      <div class="form-group flex-row">
        <label style="width: 200px;">高密度脂蛋白(HDL-C)</label>
        <input type="number" v-model="HDL" /><span class="unit">mmol/L</span>
      </div>
      <div class="form-group flex-row">
        <label style="width: 200px;">低密度脂蛋白(LDL-C)</label>
        <input type="number" v-model="LDL" /><span class="unit">mmol/L</span>
      </div>
      <div class="form-group flex-row">
        <label style="width: 200px;">脂蛋白(a)(LP(a))</label>
        <input type="number" v-model="LP" /><span class="unit">mg/L</span>
      </div>
      <div class="form-group flex-row">
        <label style="width: 200px;">载脂蛋白A1(apoA1)</label>
        <input type="number" v-model="AI" /><span class="unit">g/L</span>
      </div>
      <div class="form-group flex-row">
        <label style="width: 200px;">载脂蛋白B(apoB)</label>
        <input type="number" v-model="apoB" /><span class="unit">g/L</span>
      </div>
      <div class="form-group flex-row">
        <label style="width: 200px;">同型半胱氨酸</label>
        <input type="number" v-model="hc" /><span class="unit">g/L</span>
      </div>
      <div class="form-group flex-row">
        <label style="width: 200px;">高敏C反应蛋白(hs-CRP)</label>
        <input type="number" v-model="hs_crp" /><span class="unit">mg/L</span>
      </div>
      <div class="form-group flex-row">
        <label style="width: 200px;">糖化血红蛋白(HbA1c)</label>
        <input type="number" v-model="hb" /><span class="unit">%</span>
      </div>
    </div>
  </div>
</template>

<script>
import MButton from '../lib/Button.vue'
export default {
  name: 'temp2',
  components: {
    MButton
  },
  props: {
    mdid: String,
    templateid: String,
    recordid: String,
    source: {
      type: Object,
      default: {}
    }
  },
  data () {
    return {
      date: '',
      Cr: '',
      BUN: '',
      UA: '',
      Glu: '',
      Tcho: '',
      Tg: '',
      HDL: '',
      LDL: '',
      LP: '',
      AI: '',
      apoB: '',
      hc: '',
      hs_crp: '',
      hb: ''
    }
  },
  created () {
    if (JSON.stringify(this.source) !== '{}') {
      this.date = this.source.date
      this.Cr = this.source.Cr
      this.BUN = this.source.BUN
      this.UA = this.source.UA
      this.Glu = this.source.Glu
      this.Tcho = this.source.Tcho
      this.Tg = this.source.Tg
      this.HDL = this.source.HDL
      this.LDL = this.source.LDL
      this.LP = this.source.LP
      this.AI = this.source.AI
      this.apoB = this.source.apoB
      this.hc = this.source.hc
      this.hs_crp = this.source.hs_crp
      this.hb = this.source.hb
    }
    // 监听所有绑定值，emit 返回父级
    for (let n in this.$data) {
      this.$watch(n, (val, oldVal) => {
        this.$emit('putsever', {sourceData: this.$data, showData: this.changeData()})
      })
    }
  },
  methods: {
    // 显示数据
    changeData () {
      let showData = {
        '检查日期': this.education,
        '肌酐(Cr)': this.Cr + 'umol/L',
        '尿素氮(BUN)': this.BUN + 'umol/L',
        '尿酸(UA)': this.UA + 'umol/L',
        '血糖(Glu)': this.Glu + 'umol/L',
        '总胆固醇(TCHO)': this.Tcho + 'mmol/L',
        '甘油三酯(TG)': this.Tg + 'mmol/L',
        '高密度脂蛋白(HDL-C)': this.HDL + 'mmol/L',
        '低密度脂蛋白(LDL-C)': this.LDL + 'mmol/L',
        '脂蛋白(a)(LP(a))': this.LP + 'mg/L',
        '载脂蛋白A1(apoA1)': this.AI + 'g/L',
        '载脂蛋白B(apoB)': this.apoB + 'g/L',
        '同型半胱氨酸': this.hc + 'g/L',
        '高锰C反应蛋白(hs-CRP)': this.hs_crp + 'mg/L',
        '糖化血红蛋白(HbA1c)': this.hb + '%'
      }

      return showData
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .form-group {
    padding: 20px;
    border-bottom: 1px solid #eee;
  }
</style>



